.sidebarOptions {
    display: flex;
    color: grey ;
    height: 40px;
    cursor: pointer;
    align-items: center;
    transition: 200ms color ease-in;
} 

.sidebarOptions > p {
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}

.sidebar > hr{
    border: 1px solid #282828;
    width: 90%;
    margin: 10px auto;

}

.sidebarOptions:hover {
    color: white;
  }

  .sidebarOptions_icon {
      padding-left: 10px;
      padding-right: 10px;
  }